body {
    width: 500px;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.photo img {
    width: 200px;
    height: 200px;
    border-radius: 200px;
    position: relative;
}

.people {
    column-count: 2;
}

.extra {
    visibility: hidden;
}

.person {
    text-align: center;
}

.name {
    size: 20px;
    color: #2F4F4F;
}

.rank {
    position: absolute;
    line-height: 200px;
    color: #8B0000;
}